import { Link } from 'react-router-dom';
import './Navbar.css';

const navList = [
    { id: 1, link: '/', title: '' },
    { id: 2, link: '/about', title: ' ' },
    { id: 3, link: '/support', title: '' },
    { id: 4, link: '/social', title: ' ' },
    { id: 5, link: '/contacts', title: '' },
];

export const Navbar = () => {
    return (
        <nav className='navbar'>

            {navList.map((item) => (
                <Link to={item.link} key={item.id} className='navbar__item'>
                    {item.title}
                </Link>
            ))}

        </nav>
    )
}

